.pfp {
	position: relative;
	display: grid;
	grid-template-columns: 1fr 112px 1fr;
	justify-content: center;
}
.pfp__image {
	border-radius: 50%;
	width: 112px;
	height: 112px;
	object-fit: cover;
}
.pfp__quip-container {
	position: relative;
}
.pfp__quip--one .pfp__quip-text {
	transform: rotate(18deg);
	position: relative;
	right: -24px;
	top: -6px;
}
.pfp__quip--one .pfp__quip-arrow {
	transform: rotate(7deg);
	position: absolute;
	right: -10px;
	top: -10px;
}
@media screen and (max-width: 820px) {
	.pfp__quip--one .pfp__quip-text {
		transform: rotate(22deg);
		position: absolute;
		right: 10px;
		top: -5px;
	}
	.pfp__quip--one .pfp__quip-arrow {
		transform: rotate(7deg);
		position: absolute;
		right: -10px;
		top: -10px;
	}
}
.pfp__quip--two {
	display: flex;
	gap: 20px;
	margin-top: 15px;
}
.pfp__quip--two .pfp__quip-text {
	transform: rotate(-15deg);
	position: relative;
	top: 10px;
	right: -20px;
	max-width: 85px;
	transform-origin: center;
}
.pfp__quip--two .pfp__quip-arrow {
	transform: rotate(15deg);
}
@media screen and (max-width: 820px) {
	.pfp__quip--two .pfp__quip-text {
		position: absolute;
		top: unset;
		bottom: 0;
		right: 40px;
	}
	.pfp__quip--two .pfp__quip-arrow {
		position: absolute;
		right: 3px;
		bottom: 25px;
	}
}
.pfp__quip--three .pfp__quip-text {
	position: relative;
	bottom: -40px;
	right: -30%;
	transform: rotate(-18deg);
	font-size: 16px;
}
.pfp__quip--three .pfp__quip-arrow {
	transform: rotate(-18deg);
	position: absolute;
	left: 10px;
	top: 20px;
}
@media screen and (max-width: 820px) {
	.pfp__quip--three .pfp__quip-text {
		position: absolute;
		bottom: 12px;
		left: 30px;
	}
	.pfp__quip--three .pfp__quip-arrow {
		transform: rotate(-6deg);
		position: absolute;
		left: 10px;
		bottom: 60px;
	}
}
.pfp__quip-text {
	display: table;
	font-family: "Delicious Handrawn", sans-serif;
	font-size: 14px;
	line-height: 1.5;
	color: #000;
}
.pfp__bubble {
	position: absolute;
	pointer-events: none;
	opacity: 0;
	transform: translateY(20%);
	bottom: 82%;
	left: 47%;
	width: 175px;
	height: 100px;
	z-index: 1;
}
.pfp__bubble--show {
	animation: bubble-in-out 7s ease-in-out;
}
@keyframes bubble-in-out {
	0% {
		opacity: 0;
		transform: translateY(0) scale(0.5);
	}
	5% {
		opacity: 1;
		transform: translateY(0) scale(1.2);
	}
	7% {
		opacity: 1;
		transform: translateY(0) scale(1);
	}
	90% {
		opacity: 1;
		transform: translateY(0) scale(1);
	}
	100% {
		opacity: 0;
		transform: translateY(0) scale(0.5);
	}
}
.pfp__bubble-blockquote {
	background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/4273/speech-bubble.svg);
	padding-top: 22%;
	padding-bottom: 0;
	font-family: "Bangers", sans-serif;
