.idcta {
	appearance: none;
	border: none;
	background-color: transparent;
	outline: none;
	outline-style: none;
	outline-color: transparent;
	padding: 0;
	position: fixed;
	top: 20px;
	right: 20px;
	height: 34px;
	width: 45px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 7px;
	cursor: pointer;
	filter: brightness(100%) invert(100%);
	transition: filter 0.3s ease, opacity 0.15s ease-in-out;
}
.idcta:focus {
	outline: none;
	outline-style: none;
	outline-color: transparent;
}
.idcta::before {
	content: "";
	position: absolute;
	top: 5px;
	right: 0;
	bottom: 0;
	left: -5px;
	margin: auto;
	height: 17px;
	width: 22px;
	border-radius: 3px;
	z-index: -1;
	opacity: 0;
	background-color: rgba(0, 0, 0, 0.3);
}
.idcta--open {
	filter: brightness(0) invert(0);
}
.idcta--open::before {
	opacity: 1;
}
.idcta__icon {
	display: flex;
	height: 22px;
	margin: 0 auto;
}

